/*----- Filezone -----*/

.SN_filezone{
  border:1px solid #d3d3d3;
  border-radius:6px;
  .file_box{
    height:124px;
  }
  .upload_guide_box{
    @extend %flex;
    align-items:center;
    justify-content:center;
    padding:36px 10px;
    box-sizing:border-box;
    .file_icon{
      display:inline-block;
      vertical-align:top;
      margin-right:10px;
      width:50px;
      height:52px;
      background:url($spimg_url) 0 -219px no-repeat;
      background-size:$spimg_size;
    }
    .guide_text{
      font-size:12px;
      line-height:18px;
      color:#969696;
    }
  }
  .file_btnbox{
    padding:8px 0;
    border-top:1px solid #d3d3d3;
    text-align:center;
    .SN_btn{
      +.SN_btn{
        margin-left:4px;
      }
    }
  }
  .icon_upload{
    margin-top:8px;
    margin-right:4px;
    width:13px;
    height:13px;
    background-position:-192px -81px;
  }
  .icon_cloud{
    margin-top:9px;
    margin-right:4px;
    width:16px;
    height:12px;
    background-position:-210px -81px;
  }
}